<template>
  <div>
    <span class="toggle-btn" @click="toggleCollapse">
      <i class="el-icon-s-unfold" v-if="!show"></i>
      <i class="el-icon-s-fold" v-else></i>
    </span>
    <el-dropdown @command="handleCommand" class="userInfo">
      <span class="el-dropdown-link">
        <i class="el-icon-user"></i>
        {{ userName }}
        <i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="goToHelloWorld">退出登录</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import Utils from "../../assets/js/util.js";
import AdminNav from "./AdminNav";
export default {
  name: "AdminHeading",
  components: { AdminNav },
  data: function () {
    return {
      userAccount: "",
      userName: "",
      show: false,
    };
  },
  mounted: function () {
    this.userAccount = this.cookie.getCookie("userAccount");
    this.userName = this.cookie.getCookie("userName");
  },
  methods: {
    goToHelloWorld: function () {
      this.cookie.clearCookie("userAccount");
      this.cookie.clearCookie("userName");
      this.$router.replace("/");
    },
    toggleCollapse: function () {
      this.show = !this.show;
      Utils.$emit("toggleCollapseAdmin", "call function toggleCollapse in Admin");
    },
    handleCommand(command) {
      this.goToHelloWorld();
    },
  },
};
</script>

<style scoped>
@import "../../assets/css/head.css";
</style>



